<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject">染色体管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :inline="true" :model="browForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="browForm.CreateDate" readonly  style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="browForm.LatestUpdateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="受检者姓名" prop="Name">
            <Input v-model="browForm.Name" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="出生日期">
            <DatePicker v-model="browForm.DateOfBirth" :value="browForm.DateOfBirth" readonly show-week-numbers :options="DataPickerOption" placement="bottom-start" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="性别">
            <Input v-model="browForm.gender" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="证件类型">
            <Input v-model="browForm.IDType" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="证件号码">
            <Input v-model="browForm.IDNumber" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="手机号码">
            <Input v-model="browForm.PhoneNumber" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="邮箱地址">
            <Input v-model="browForm.Email" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="家族史">
            <Input v-model="browForm.familyHistory" readonly type="textarea" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="diagnosisCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addDiagnosis">添加新临床诊疗</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border height="200" :loading="loading" ref="selection" :columns="diagnosisTableHead" :data="diagnosisTableData" @on-selection-change="diagHandleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn">移除选中的{{diagMultipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="diagnosisTotalNum" :current="diagPageNum" :page-size="diagPageSize" show-elevator placement="top" size="small" @on-change="diagHandlePage" @on-page-size-change='diagHandlePageSize'></Page>
              </div>
            </div>
          </div>
          <div class="molDiagnosisCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addMolDiagnosis">添加新分子诊断</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border height="200" :loading="molLoading" ref="selection" :columns="molDiagnosisTableHead" :data="molDiagnosisTableData" @on-selection-change="molDiagHandleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="molDiagnosisTotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="molDiagHandlePage" @on-page-size-change='molDiagHandlePageSize'></Page>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/subject/index.php",qs.stringify({SubjectID:this.subjectID})).then(response=>{
        console.log(response.data);
        this.browForm = response.data.data[0]
      })
      this.loading = false;
      this.molLoading = false;
    },
    data() {
      return {
        subjectID:location.search.split("=")[1],
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        browForm:{
          Name:'',
          DateOfBirth:'',
          Gender:'',
          IDType:'',
          IDNumber:'',
          PhoneNumber:'',
          Efront:'',
          Eback:'@163.com',
          Email:'',
          FamilyHistory:''
        },
        rule:{
          SubjectName:[
            {
              required: true, message: '染色体名称不能为空', trigger: 'blur'
            }
          ]
        },
        loading: true,
        molLoading: true,
        diagnosisTableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"临床诊疗编号",
            key:'diagnosisID',
            align: 'center'
          },
          {
            title:"疾病分级",
            key:'diseaseLevel',
            align: 'center'
          },
          {
            title:"疾病分期",
            key:'disperiod',
            align: 'center'
          },
          {
            title:"疾病治疗历史",
            key:'disHistory',
            align: 'center'
          },
          {
            title:"临床诊断描述",
            key:'disDesc',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.diagShow(params.row.diagnosisID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.diagEdit(params.row.diagnosisID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.diagRemove(params.row.diagnosisID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        diagnosisTableData:[
          {
            diagnosisID:'111',
            diseaseLevel:'111',
            disperiod:'111',
            disHistory:'111',
            disDesc:'111'
          }
        ],
        diagnosisTotalNum:0,
        diagMultipleSelection:[],
        diagPageNum:1,
        diagPageSize:10,
        molDiagnosisTableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"分子诊断编号",
            key:'SubjectID',
            align: 'center'
          },
          {
            title:"分子诊断产品",
            key:'SubjectName',
            align: 'center'
          },
          {
            title:"状态",
            key:'Status',
            align: 'center'
          },
          {
            title:"报告人",
            key:'reportor',
            align: 'center'
          },
          {
            title:"审核人",
            key:'reviewer',
            align: 'center'
          },
          {
            title:"报告日期",
            key:'reportDate',
            align: 'center'
          },
          {
            title:"分子诊断报告",
            key:'report',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.molDiagShow(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.molDiagEdit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.molDiagRemove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        molDiagnosisTableData:[
          {
            SubjectID:'111',
            SubjectName:'111',
            Status:'111',
            reportor:'111',
            reviewer:'111',
            reportDate:'111',
            report:'111'
          }
        ],
        molDiagnosisTotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editsubject")
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            
          }
        })
      },
      backList(){
        this.$router.push("/subject")
      },
      addDiagnosis(){
        //将当前受检者的ID传入
        this.$router.push("/adddiagnosis")
      },
      diagShow (id) {
        // console.log(id);
        this.$router.push("/browdiagnosis")
      },
      diagEdit (id) {
        // console.log(id);
        this.$router.push("/editdiagnosis")
      },
      diagRemove(id){
        // console.log(id);        
      },
      diagHandleSelectionChange(val){
        this.diagMultipleSelection = val;
      },
      diagHandlePage(val){
        this.diagPageNum = val;
      },
      diagHandlePageSize(val){
        this.diagPageSize = val;
      },
      addMolDiagnosis(){
        //将当前受检者的ID传入
        this.$router.push("/addmoldiagnosis")
      },
      molDiagShow (id) {
        // console.log(id);
        this.$router.push("/browmoldiagnosis")
      },
      molDiagEdit (id) {
        // console.log(id);
        this.$router.push("/editmoldiagnosis")
      },
      molDiagRemove(id){
        // console.log(id);        
      },
      molDiagHandleSelectionChange(val){
        this.multipleSelection = val;
      },
      molDiagHandlePage(val){
        this.pageNum = val;
      },
      molDiagHandlePageSize(val){
        this.pageSize = val;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .diagnosisCon{
      box-sizing:border-box;
      border-bottom:1px dashed #ccc;
    }
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


