<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/moldiagnosis">分子诊断管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的分子诊断</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的分子诊断</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的分子诊断</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="受检人编号" prop="SubjectID">
            <Input v-model="addForm.SubjectID" placeholder="请输入受检人编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="受检人姓名">
            <Input v-model="addForm.SubjectName" placeholder="请输入受检人姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检日期">
            <Input v-model="addForm.checkDate" placeholder="请输入送检日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检医师">
            <Input v-model="addForm.doctor" placeholder="请输入送检医师" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检机构">
            <Input v-model="addForm.organization" placeholder="请输入送检机构" style="width: 280px"></Input>
          </FormItem>
          <div>
            <h2>产品名称：</h2>
            <FormItem label="Panel名称">
              <Input v-model="addForm.PanelName" placeholder="请输入Panel名称" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Product名称">
              <Input v-model="addForm.ProductName" placeholder="请输入Product名称" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Pipeline名称">
              <Input v-model="addForm.PipelineName" placeholder="请输入Pipeline名称" style="width: 280px"></Input>
            </FormItem>
          </div>
          <FormItem label="状态">
            <Input v-model="addForm.status" placeholder="请输入状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="报告人">
            <Input v-model="addForm.reportor" placeholder="请输入报告人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="审阅人">
            <Input v-model="addForm.reviewer" placeholder="请输入审阅人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="报告日期">
            <Input v-model="addForm.reportDate" placeholder="请输入报告日期" style="width: 280px"></Input>
          </FormItem>     
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="handleSubmit('addForm')">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          SubjectID:'111',
          SubjectName:'',
          checkDate:'',
          doctor:'',
          organization:'',
          PanelName:'',
          ProductName:'',
          PipelineName:'',
          status:'',
          reportor:'',
          reviewer:'',
          reportDate:''
        },
        rule:{
          SubjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
              this.$router.push('/moldiagnosis')
            } else {
                this.$Message.error('Fail!');
            }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


