<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem to="/browlibrary">查看文库</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='browvariant'">查看QC</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='editvariant'">编辑QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请输入最后更新日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库公司编号" prop="libID">
            <Input v-model="editForm.libID" disabled placeholder="请输入文库公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体名称" prop="ChrName">
            <Input v-model="editForm.ChrName" :disabled="currentPath==='browvariant'" placeholder="请输入染色体名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体位置" prop="Position">
            <Input v-model="editForm.Position" :disabled="currentPath==='browvariant'" placeholder="请输入染色体位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <Input v-model="editForm.Symbol" :disabled="currentPath==='browvariant'" placeholder="请输入Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本">
            <Input v-model="editForm.transcript" :disabled="currentPath==='browvariant'" placeholder="请输入转录本" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="REF序列">
            <Input v-model="editForm.ref" :disabled="currentPath==='browvariant'" placeholder="请输入REF序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT序列">
            <Input v-model="editForm.alt" :disabled="currentPath==='browvariant'" placeholder="请输入ALT序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异类型">
            <Input v-model="editForm.style" :disabled="currentPath==='browvariant'" placeholder="请输入变异类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因型">
              <Input v-model="editForm.genotype" :disabled="currentPath==='browvariant'" placeholder="请输入基因型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因频率">
              <Input v-model="editForm.geneFrequency" :disabled="currentPath==='browvariant'" placeholder="请输入基因频率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="测序深度">
              <Input v-model="editForm.genotype" :disabled="currentPath==='browvariant'" placeholder="请输入测序深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="突变结果">
              <Input v-model="editForm.geneFrequency" :disabled="currentPath==='browvariant'" placeholder="请输入突变结果" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="editForm.exon" :disabled="currentPath==='browvariant'" placeholder="请输入exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron位置">
              <Input v-model="editForm.exonPosition" :disabled="currentPath==='browvariant'" placeholder="请输入exon/intron位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
              <Input v-model="editForm.hgvsp" :disabled="currentPath==='browvariant'" placeholder="请输入HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="editForm.hgvsc" :disabled="currentPath==='browvariant'" placeholder="请输入HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="rsID">
            <Input v-model="editForm.rsID" :disabled="currentPath==='browvariant'" placeholder="rsID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="editForm.clini" :disabled="currentPath==='browvariant'" placeholder="请输入临床意义" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="标记">
            <Input v-model="editForm.sign" :disabled="currentPath==='browvariant'" placeholder="请输入标记" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit" v-if="currentPath=='browvariant'">编辑</Button>
              <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath=='editvariant'">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[1],
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestUpdateDate:'111',
          libID:'111',
          ChrName:'111',
          Position:'111',
          Symbol:'111',
          transcript:'111',
          ref:'111',
          alt:'111',
          style:'111',
          genotype:'111',
          geneFrequency:'111',
          exon:'111',
          exonPosition:'111',
          hgvsp:'111',
          hgvsc:'111',
          rsID:'111',
          clini:'111',
          sign:'111'
        },
        rule:{
          libID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          ChrName:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          Position:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          Symbol:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleEdit(){
        this.currentPath="editvariant"
        this.$router.push("/editvariant")
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/browlibrary')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


