<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem to="/browpanel">查看panel</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <DatePicker v-model="editForm.CreateDate" disabled show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="editForm.LatestUpdateDate" disabled show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel项目编号" prop="PanelID">
            <Input v-model="editForm.PanelID" disabled placeholder="请输入Panel项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel升级记录编号" prop="PanelRecID">
            <Input v-model="editForm.PanelRecID" disabled placeholder="请输入Panel升级记录编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel版本号" prop="PanelVisionID">
            <Input v-model="editForm.PanelVisionID" placeholder="请输入Panel版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel方法" prop="PanelMethod">
            <Input v-model="editForm.PanelMethod" placeholder="请输入Panel方法" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel状态">
            <Input v-model="editForm.PanelOnline" placeholder="请输入Panel状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel启动日期">
            <DatePicker v-model="editForm.PanelStartDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择Panel启动日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel结束日期">
            <DatePicker v-model="editForm.PanelFinishDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择Panel结束日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel描述">
            <Input type="textarea" v-model="editForm.PanelDesc" placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          PanelID:'111',
          PanelRecID:'111',
          PanelVisionID:'111',
          PanelMethod:'111',
          PanelOnline:'111',
          PanelStartDate:'2018-01-01',
          PanelFinishDate:'2018-01-01',
          PanelDesc:'111',
          CreateDate:'2018-01-01',
          LatestUpdateDate:'2018-01-01'
        },
        rule:{
          PanelID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelRecID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelVisionID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelMethod:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            
          }
        })
      },
      backList(){
        this.$router.push("/browpanel")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


