<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem to="/browlibrary">查看文库</BreadcrumbItem>
      <BreadcrumbItem>添加新的QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的QC</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的QC</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="文库公司编号" prop="libID">
            <Input v-model="addForm.libID" placeholder="请输入文库公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="总碱基数" prop="totalbase">
            <Input v-model="addForm.totalbase" placeholder="请输入总碱基数" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="总序列数">
            <Input v-model="addForm.totalSequence" placeholder="请输入总序列数" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="重复率">
            <Input v-model="addForm.repetitionRate" placeholder="请输入重复率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="对比率">
              <Input v-model="addForm.compareRate" placeholder="请输入对比率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="序列长度">
              <Input v-model="addForm.sequenceLength" placeholder="请输入序列长度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="捕获效率">
              <Input v-model="addForm.captureEfficiency" placeholder="请输入捕获效率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针平均深度">
              <Input v-model="addForm.probeAverage" placeholder="请输入探针平均深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针中点深度">
            <Input v-model="addForm.probeMiddele" placeholder="请输入探针中点深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针均一性">
            <Input v-model="addForm.probeHomogeneity" placeholder="请输入探针均一性" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="覆盖率">
            <Input v-model="addForm.coverRate" placeholder="请输入覆盖率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="覆盖率≥30">
            <Input v-model="addForm.coverRate30" placeholder="请输入覆盖率≥30" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="addForm.status" placeholder="请选择状态" style="width: 280px">
                <Option value="合格">合格</Option>
                <Option value="不合格">不合格</Option>
              </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          libID:'',
          totalbase:'',
          totalSequence:'',
          repetitionRate:'',
          compareRate:'',
          sequenceLength:'',
          captureEfficiency:'',
          probeAverage:'',
          probeMiddele:'',
          probeHomogeneity:'',
          coverRate:'',
          coverRate30:'',
          status:''
        },
        rule:{
          libID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          totalbase:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
              this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/browlibrary')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


