<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请输入最后更新日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸公司编号" prop="nucleicAcidID">
            <Input v-model="editForm.nucleicAcidID" disabled placeholder="请输入核酸公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库公司编号" prop="libID">
            <Input v-model="editForm.libID" disabled placeholder="请输入文库公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建人">
              <Input v-model="editForm.collector" placeholder="请输入文库提取人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建日期">
            <DatePicker v-model="editForm.collectDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择文库提取日期（起始时间段）" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="文库构建用量">
            <Input v-model="editForm.useRange" placeholder="请输入文库构建用量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建用量单位">
            <Input v-model="editForm.useRangeUnit" placeholder="请输入文库构建用量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积">
              <Input v-model="editForm.measure" placeholder="请输入文库体积" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积单位">
              <Input v-model="editForm.measureUnit" placeholder="请输入文库体积单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度">
              <Input v-model="editForm.density" placeholder="请选择文库浓度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度单位">
              <Input v-model="editForm.densityUnit" placeholder="请选择文库浓度单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量">
            <Input v-model="editForm.total" placeholder="请输入文库总量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量单位">
            <Input v-model="editForm.totalUnit" placeholder="请输入文库总量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小">
            <Input v-model="editForm.insert" placeholder="请输入文库插入片段大小" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小单位">
            <Input v-model="editForm.insertUnit" placeholder="请输入文库插入片段大小单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库质量">
              <Select v-model="editForm.quality" placeholder="请选择文库质量" style="width: 280px">
                <Option value="好">好</Option>
                <Option value="中">中</Option>
                <Option value="差">差</Option>
              </Select>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="editForm.status" placeholder="请选择状态" style="width: 280px">
                <Option value="合格">合格</Option>
                <Option value="不合格">不合格</Option>
              </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestUpdateDate:'111',
          nucleicAcidID:'111',
          libID:'111',
          collector:'111',
          collectDate:'2018-01-01',
          useRange:'111',
          useRangeUnit:'111',
          measure:'111',
          measureUnit:'111',
          density:'111',
          densityUnit:'111',
          total:'111',
          totalUnit:'111',
          insert:'111',
          insertUnit:'111',
          quality:'好',
          status:'合格'
        },
        rule:{
          nucleicAcidID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          libID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/library')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


