<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>文库管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="文库公司编号">
            <Input v-model="searchForm.libID" placeholder="请输入文库公司编号" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库构建用量">
            <Select v-model="searchForm.useRange" placeholder="请选择文库提取用量(范围)" style="width: 162px">
              <Option value="1111">1111</Option>
              <Option value="2222">2222</Option>
            </Select>
          </FormItem>
          <FormItem label="文库构建人">
              <Input v-model="searchForm.collector" placeholder="请输入文库提取人" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库构建日期">
            <DatePicker type="daterange" v-model="searchForm.collectDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择文库提取日期（起始时间段）" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="文库体积(范围)">
              <Input v-model="searchForm.measure" placeholder="请输入文库体积(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库浓度(范围)">
              <Input v-model="searchForm.density" placeholder="请选择文库浓度(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库总量(范围)">
            <Input v-model="searchForm.total" placeholder="请输入文库总量(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小(范围)">
            <Input v-model="searchForm.insert" placeholder="文库插入片段大小(范围)" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="文库质量">
              <Select v-model="searchForm.quality" placeholder="请选择文库质量" style="width: 162px">
                <Option value="好">好</Option>
                <Option value="中">中</Option>
                <Option value="差">差</Option>
              </Select>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="searchForm.status" placeholder="请选择状态" style="width: 162px">
                <Option value="合格">合格</Option>
                <Option value="不合格">不合格</Option>
              </Select>
          </FormItem>
          <FormItem label="创建日期">
            <DatePicker type="daterange" v-model="searchForm.CreateDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker type="daterange" v-model="searchForm.LatestUpdateDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addLib">添加新文库</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        searchForm:{
          libID:'',
          useRange:'',
          collector:'',
          collectDate:'',
          measure:'',
          density:'',
          total:'',
          insert:'',
          quality:'',
          status:'',
          CreateDate:'',
          LatestUpdateDate:''
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"核酸公司编号",
            key:'nucleicAcidID',
            align: 'center'
          },
          {
            title:"文库公司编号",
            key:'libID',
            align: 'center'
          },
          {
            title:"文库构建用量及单位",
            key:'useRange',
            align: 'center'
          },
          {
            title:"文库构建日期",
            key:'CollectDate',
            align: 'center'
          },
          {
            title:"文库构建人",
            key:'Collector',
            align: 'center'
          },
          {
            title:"文库质量",
            key:'libQuality',
            align: 'center'
          },
          {
            title:"文库体积及单位",
            key:'libMeasure',
            align: 'center'
          },
          {
            title:"文库浓度及单位",
            key:'libDensity',
            align: 'center'
          },
          {
            title:"文库总量及单位",
            key:'libTotal',
            align: 'center'
          },
          {
            title:"文库插入片段大小",
            key:'libInsert',
            align: 'center'
          },
          {
            title:"状态",
            key:'status',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            nucleicAcidID:'111',
            libID:'111',
            useRange:'111',
            CollectDate:'111',
            Collector:'111',
            libQuality:'111',
            libMeasure:'111',
            libDensity:'111',
            libTotal:'111',
            libInsert:'111',
            status:'111',
            CreateDate:'111',
            LatestUpdateDate:'111'
          }
        ],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        
      },
      addLib(){
        this.$router.push("/addlibrary")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browlibrary")
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editlibrary")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
      },
      handlePageSize(val){
        this.pageSize = val;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


