<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/moldiagnosis">分子诊断管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm"  :inline="true" :model="browForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="受检人编号" prop="SubjectID">
            <Input v-model="browForm.SubjectID" placeholder="请输入受检人编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="受检人姓名">
            <Input v-model="browForm.SubjectName" placeholder="请输入受检人姓名" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检日期">
            <Input v-model="browForm.checkDate" placeholder="请输入送检日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检医师">
            <Input v-model="browForm.doctor" placeholder="请输入送检医师" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="送检机构">
            <Input v-model="browForm.organization" placeholder="请输入送检机构" style="width: 280px"></Input>
          </FormItem>
          <div>
            <h2>产品名称：</h2>
            <FormItem label="Panel名称">
              <Input v-model="browForm.PanelName" placeholder="请输入Panel名称" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Product名称">
              <Input v-model="browForm.ProductName" placeholder="请输入Product名称" style="width: 280px"></Input>
            </FormItem>
            <FormItem label="Pipeline名称">
              <Input v-model="browForm.PipelineName" placeholder="请输入Pipeline名称" style="width: 280px"></Input>
            </FormItem>
          </div>
          <FormItem label="状态">
            <Input v-model="browForm.status" placeholder="请输入状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="报告人">
            <Input v-model="browForm.reportor" placeholder="请输入报告人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="审阅人">
            <Input v-model="browForm.reviewer" placeholder="请输入审阅人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="报告日期">
            <Input v-model="browForm.reportDate" placeholder="请输入报告日期" style="width: 280px"></Input>
          </FormItem>     
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="handleSubmit('browForm')">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="addContainer">
            <Button type="warning" size="large" @click="addBioSpeciemens">添加新样本</Button>
          </div>
          <div class="chromoListcontainer">
            <Table stripe border height="200" :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
            <div class="batchContainer">
              <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
            </div>
            <div class="pageContainer">
              <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        chrID:location.search.split("=")[1],
        browForm:{
          SubjectID:'111',
          SubjectName:'',
          checkDate:'',
          doctor:'',
          organization:'',
          PanelName:'',
          ProductName:'',
          PipelineName:'',
          status:'',
          reportor:'',
          reviewer:'',
          reportDate:''
        },
        rule:{
          SubjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"样本编号",
            key:'bioID',
            align: 'center'
          },
          {
            title:"样本类型",
            key:'bioStyle',
            align: 'center'
          },
          {
            title:"取样部位",
            key:'samplingSite',
            align: 'center'
          },
          {
            title:"取样日期",
            key:'samplingDate',
            align: 'center'
          },
          {
            title:"收样日期",
            key:'collectionDate',
            align: 'center'
          },
          {
            title:"收样人",
            key:'Sampler',
            align: 'center'
          },
          {
            title:"是否为肿瘤细胞",
            key:'isCancerCell',
            align: 'center'
          },
          {
            title:"肿瘤细胞含量",
            key:'cancerCellPrecent',
            align: 'center'
          },
          {
            title:"病理编号",
            key:'pathologyID',
            align: 'center'
          },
          {
            title:"样本量",
            key:'sampleMount',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            bioID:'111',
            bioStyle:'111',
            samplingSite:'111',
            samplingDate:'111',
            collectionDate:'111',
            Sampler:'111',
            isCancerCell:'111',
            cancerCellPrecent:'111',
            pathologyID:'111',
            sampleMount:'111'
          }
        ],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editmoldiagnosis")
      },
      addBioSpeciemens(){
        this.$router.push("/addbiospeciemens")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browbiospeciemens")
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editbiospeciemens")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
      },
      handlePageSize(val){
        this.pageSize = val;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .chromoListcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:5px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding: 10px;
      }
    }
  }
}
</style>


