<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>产品管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="产品编号">
            <Input v-model="searchForm.PanelID" placeholder="请输入产品编号" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="产品中文名称">
            <Input v-model="searchForm.PanelName_cn" placeholder="请输入产品中文名称" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="产品英文名称">
            <Input v-model="searchForm.PanelName_en" placeholder="请输入产品英文名称" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="创建日期">
            <DatePicker type="daterange" v-model="searchForm.CreateDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker type="daterange" v-model="searchForm.LatestUpdateDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addPanel">添加新产品</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        searchForm:{
          PanelID:'',
          PanelName_cn:'',
          PanelName_en:'',
          CreateDate:'',
          LatestUpdateDate:''
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"产品编号",
            key:'PanelID',
            align: 'center'
          },
          {
            title:"产品中文名称",
            key:'PanelName_cn',
            align: 'center'
          },
          {
            title:"产品英文名称",
            key:'PanelName_en',
            align: 'center'
          },
          {
            title:"Panel 中文名称及版本号",
            key:'PanelName_en_Vis',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            PanelID:'111',
            PanelName_cn:'111',
            PanelName_en:'111',
            PanelName_en_Vis:'111',
            CreateDate:'111',
            LatestUpdateDate:'111'
          }
        ],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        
      },
      addPanel(){
        this.$router.push("/addproduct")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browproduct")
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editproduct")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
      },
      handlePageSize(val){
        this.pageSize = val;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


