<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject">受检者管理</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='browdiagnosis'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='editdiagnosis'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="受检者编号" prop="SubjectID">
            <Input v-model="editForm.SubjectID" disabled placeholder="请输入受检者编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床诊疗编号">
            <Input v-model="editForm.diagID" disabled placeholder="请输入临床诊疗编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床诊疗描述">
            <Input v-model="editForm.diagDesc" :disabled="currentPath==='browdiagnosis'" type="textarea" placeholder="请输入临床诊疗描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病分级">
            <Input v-model="editForm.dislevel" :disabled="currentPath==='browdiagnosis'" placeholder="请输入疾病分级" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病分期">
            <Input v-model="editForm.disPeriod" :disabled="currentPath==='browdiagnosis'" placeholder="请输入疾病分期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病治疗历史">
            <Input v-model="editForm.disHistory" :disabled="currentPath==='browdiagnosis'" placeholder="请输入疾病治疗历史" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit"  v-if="currentPath==='browdiagnosis'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath==='editdiagnosis'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split("/")[1],
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          SubjectID:'',
          diagID:'',
          diagDesc:'',
          dislevel:'',
          disPeriod:'',
          disHistory:''
        },
        rule:{
          SubjectID:[
            {
              required: true, message: '染色体名称不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleEdit(){
        this.currentPath="editdiagnosis";
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            
          }
        })
      },
      backList(){
        this.$router.push("/browsubject")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


