<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <DatePicker v-model="editForm.CreateDate" disabled show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="editForm.LatestUpdateDate" disabled show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品编号">
            <Input v-model="editForm.ProductID" readonly placeholder="请输入产品编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel中文名称" prop="PanelName_cn">
            <Input v-model="editForm.PanelName_cn" placeholder="请输入Panel中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel版本号" prop="PanelVis">
            <Input v-model="editForm.PanelVis" placeholder="请输入Panel版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称" prop="ProductName_cn">
            <Input v-model="editForm.ProductName_cn" placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品英文名称" prop="ProductName_en">
            <Input v-model="editForm.ProductName_en" placeholder="请输入产品英文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          ProductID:'111',
          PanelName_cn:'111',
          PanelVis:'111',
          ProductName_cn:'111',
          ProductName_en:'111',
          CreateDate:'2018-01-01',
          LatestUpdateDate:'2018-01-01'
        },
        rule:{
          PanelName_cn:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelName_en:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            
          }
        })
      },
      backList(){
        this.$router.push("/product")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


