<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem to="/browlibrary">查看文库</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='browqc'">查看QC</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath=='editqc'">编辑QC</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请输入最后更新日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库公司编号" prop="libID">
            <Input v-model="editForm.libID" disabled placeholder="请输入文库公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="总碱基数" prop="totalbase">
            <Input v-model="editForm.totalbase" :disabled="currentPath=='browqc'" placeholder="请输入总碱基数" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="总序列数">
            <Input v-model="editForm.totalSequence" :disabled="currentPath=='browqc'" placeholder="请输入总序列数" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="重复率">
            <Input v-model="editForm.repetitionRate" :disabled="currentPath=='browqc'" placeholder="请输入重复率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="对比率">
              <Input v-model="editForm.compareRate" :disabled="currentPath=='browqc'" placeholder="请输入对比率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="序列长度">
              <Input v-model="editForm.sequenceLength" :disabled="currentPath=='browqc'" placeholder="请输入序列长度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="捕获效率">
              <Input v-model="editForm.captureEfficiency" :disabled="currentPath=='browqc'" placeholder="请输入捕获效率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针平均深度">
              <Input v-model="editForm.probeAverage" :disabled="currentPath=='browqc'" placeholder="请输入探针平均深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针中点深度">
            <Input v-model="editForm.probeMiddele" :disabled="currentPath=='browqc'" placeholder="请输入探针中点深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="探针均一性">
            <Input v-model="editForm.probeHomogeneity" :disabled="currentPath=='browqc'" placeholder="请输入探针均一性" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="覆盖率">
            <Input v-model="editForm.coverRate" :disabled="currentPath=='browqc'" placeholder="请输入覆盖率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="覆盖率≥30">
            <Input v-model="editForm.coverRate30" :disabled="currentPath=='browqc'" placeholder="请输入覆盖率≥30" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="editForm.status" :disabled="currentPath=='browqc'" placeholder="请选择状态" style="width: 280px">
                <Option value="合格">合格</Option>
                <Option value="不合格">不合格</Option>
              </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit" v-if="currentPath=='browqc'">编辑</Button>
              <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath=='editqc'">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[1],
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestUpdateDate:'111',
          libID:'111',
          totalbase:'111',
          totalSequence:'111',
          repetitionRate:'111',
          compareRate:'111',
          sequenceLength:'111',
          captureEfficiency:'111',
          probeAverage:'111',
          probeMiddele:'111',
          probeHomogeneity:'111',
          coverRate:'111',
          coverRate30:'111',
          status:'合格'
        },
        rule:{
          libID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          totalbase:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleEdit(){
        this.currentPath="editqc"
      },
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/browlibrary')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


