<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem to="/browpruduct">查看产品</BreadcrumbItem>
      <BreadcrumbItem>编辑产品升级记录详情</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <DatePicker v-model="editForm.CreateDate" disabled show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="editForm.LatestUpdateDate" disabled show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品编号">
            <Input v-model="editForm.PanelID" disabled placeholder="请输入产品编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品升级记录编号">
            <Input v-model="editForm.PanelRecID" disabled placeholder="请输入产品升级记录编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称">
            <Input v-model="editForm.ProductName_cn" placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品版本号">
            <Input v-model="editForm.PanelVisionID" placeholder="请输入产品版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品状态">
            <Input v-model="editForm.PanelOnline" placeholder="请输入产品状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品上线日期">
            <DatePicker v-model="editForm.PanelStartDate" placeholder="请选择产品上线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品下线日期">
            <DatePicker v-model="editForm.PanelFinishDate" placeholder="请选择产品下线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品检测的变异类型">
            <Input v-model="editForm.productVariant" placeholder="请输入产品检测的变异类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品信息分析流程">
            <Input v-model="editForm.productAnalisys" placeholder="请输入产品信息分析流程" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品信息分析流程版本号">
            <Input v-model="editForm.productOpVi" placeholder="请输入产品信息分析流程版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品描述">
            <Input type="textarea" v-model="editForm.PanelDesc" placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          PanelID:'111',
          PanelRecID:'111',
          ProductName_cn:'111',
          PanelVisionID:'111',
          PanelOnline:'111',
          PanelStartDate:'2018-01-01',
          PanelFinishDate:'2018-01-01',
          PanelDesc:'111',
          productVariant:'111',
          productAnalisys:'111',
          productOpVi:'111',
          CreateDate:'2018-01-01',
          LatestUpdateDate:'2018-01-01'
        },
        rule:{
          PanelID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelRecID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelVisionID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelMethod:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            
          }
        })
      },
      backList(){
        this.$router.push("/browproduct")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


