<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/pipeline">信息分析流程管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :model="browForm" label-position="right" :label-width="140">
          <FormItem label="创建日期">
            <DatePicker v-model="browForm.CreateDate" readonly show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="browForm.LatestUpdateDate" readonly show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="信息分析流程编号">
            <Input v-model="browForm.PanelID" readonly placeholder="请输入信息分析流程编号" style="width: 162px"></Input>
          </FormItem>
          <FormItem label="信息分析流程项目编号">
            <Input v-model="browForm.PanelproID" readonly placeholder="请输入信息分析流程项目编号" style="width: 162px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="addContainer">
            <Button type="warning" size="large" @click="addPipelineRec">添加新信息分析流程更新记录</Button>
          </div>
          <div class="listcontainer">
            <Table stripe border :loading="loading" height="200" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
            <div class="batchContainer">
              <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
            </div>
            <div class="pageContainer">
              <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
      this.molLoading = false;
    },
    data() {
      return {
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        browForm:{
          PanelID:'111',
          PanelproID:'111',
          CreateDate:'2018-01-01',
          LatestUpdateDate:'2018-01-01'
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"信息分析流程中文名称",
            key:'PanelRecID',
            align: 'center'
          },
          {
            title:"信息分析流程英文名称",
            key:'PanelMethod',
            align: 'center'
          },
          {
            title:"信息分析流程版本号",
            key:'PanelOnline',
            align: 'center'
          },
          {
            title:"信息分析流程状态",
            key:'PanelVisionID',
            align: 'center'
          },
          {
            title:"信息分析流程上线日期",
            key:'PanelStartDate',
            align: 'center'
          },
          {
            title:"信息分析流程下线日期",
            key:'PanelFinishDate',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            PanelRecID:'111',
            PanelMethod:'111',
            PanelOnline:'111',
            PanelVisionID:'111',
            PanelStartDate:'111',
            PanelFinishDate:'111',
            CreateDate:'111',
            LatestUpdateDate:'111'
          }
        ],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editpipeline")
      },
      backList(){
        this.$router.push("/pipeline")
      },
      addPipelineRec(){
        this.$router.push("/addpipelinerec")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browpipelinerec")
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editpipelinerec")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
      },
      handlePageSize(val){
        this.pageSize = val;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


