<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/nucleicacid">核酸管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的核酸提取</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的核酸提取</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的核酸提取</p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="样本编号" prop="bioID">
            <Input v-model="addForm.bioID" placeholder="请输入样本编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸公司编号" prop="nucleicAcidID">
            <Input v-model="addForm.nucleicAcidID" placeholder="请输入核酸公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取日期" prop="collectDate">
            <DatePicker v-model="addForm.collectDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择核酸提取日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="核酸提取人" prop="collector">
            <Input v-model="addForm.collector" placeholder="请输入核酸提取人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量">
            <Input v-model="addForm.useRange" placeholder="请输入核酸提取用量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量单位">
            <Input v-model="addForm.useRangeUnit" placeholder="请输入核酸提取用量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积">
              <Input v-model="addForm.measure" placeholder="请输入核酸体积" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积单位">
              <Input v-model="addForm.measureUnit" placeholder="请输入核酸体积单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度">
            <Input v-model="addForm.density" placeholder="请输入核酸浓度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度单位">
            <Input v-model="addForm.densityUnit" placeholder="请输入核酸浓度单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量">
            <Input v-model="addForm.total" placeholder="请输入核酸总量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量单位">
            <Input v-model="addForm.totalUnit" placeholder="请输入核酸总量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸质量">
            <Select v-model="addForm.quality" placeholder="请选择核酸质量" style="width: 280px">
              <Option value="好">好</Option>
              <Option value="中">中</Option>
              <Option value="坏">坏</Option>
            </Select>
          </FormItem>
          <FormItem label="核酸类型">
            <Input v-model="addForm.nucleicAcidStyle" placeholder="请输入核酸类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="addForm.status" placeholder="请选择状态" style="width: 280px">
              <Option value="合格">合格</Option>
              <Option value="不合格">不合格</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          bioID:'',
          nucleicAcidID:'',
          useRange:'',
          useRangeUnit:'',
          collector:'',
          collectDate:'',
          measure:'',
          measureUnit:'',
          density:'',
          densityUnit:'',
          total:'',
          totalUnit:'',
          quality:'',
          nucleicAcidStyle:'',
          status:''
        },
        rule:{
          bioID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          nucleicAcidID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          collector:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          collectDate:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
              this.$router.push('/nucleicacid')
            } else {
              this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/nucleicacid')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


