<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem to="/browpanel">查看panel</BreadcrumbItem>
      <BreadcrumbItem>添加新的Panel升级记录</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的Panel升级记录</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的Panel升级记录</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="Panel项目编号" prop="PanelID">
            <Input v-model="addForm.PanelID" placeholder="请输入Panel项目编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel版本号" prop="PanelVisionID">
            <Input v-model="addForm.PanelVisionID" placeholder="请输入Panel版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel方法" prop="PanelMethod">
            <Input v-model="addForm.PanelMethod" placeholder="请输入Panel方法" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel状态">
            <Input v-model="addForm.PanelOnline" placeholder="请输入Panel状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Panel启动日期">
            <DatePicker v-model="addForm.PanelStartDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择Panel启动日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel结束日期">
            <DatePicker v-model="addForm.PanelFinishDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择Panel结束日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="Panel描述">
            <Input type="textarea" v-model="addForm.PanelDesc" placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          PanelID:'',
          PanelVisionID:'',
          PanelMethod:'',
          PanelOnline:'',
          PanelStartDate:'',
          PanelFinishDate:'',
          PanelDesc:''
        },
        rule:{
          PanelID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelVisionID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelMethod:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/browpanel")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


