<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem to="/browpruduct">查看产品</BreadcrumbItem>
      <BreadcrumbItem>查看产品升级记录详情</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :inline="true" :model="browForm" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <DatePicker v-model="browForm.CreateDate" readonly placeholder="请选择创建日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="最后更新日期">
            <DatePicker v-model="browForm.LatestUpdateDate" readonly placeholder="请选择最后更新日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品编号">
            <Input v-model="browForm.PanelID" readonly placeholder="请输入产品编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品升级记录编号">
            <Input v-model="browForm.PanelRecID" readonly placeholder="请输入产品升级记录编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称">
            <Input v-model="browForm.ProductName_cn" readonly placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品版本号">
            <Input v-model="browForm.PanelVisionID" readonly placeholder="请输入产品版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品状态">
            <Input v-model="browForm.PanelOnline" readonly placeholder="请输入产品状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品上线日期">
            <DatePicker v-model="browForm.PanelStartDate" readonly placeholder="请选择产品上线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品下线日期">
            <DatePicker v-model="browForm.PanelFinishDate" readonly placeholder="请选择产品下线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品检测的变异类型">
            <Input v-model="browForm.productVariant" readonly placeholder="请输入产品检测的变异类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品信息分析流程">
            <Input v-model="browForm.productAnalisys" readonly placeholder="请输入产品信息分析流程" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品信息分析流程版本号">
            <Input v-model="browForm.productOpVi" readonly placeholder="请输入产品信息分析流程版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品描述">
            <Input type="textarea" v-model="browForm.PanelDesc" readonly placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="teamCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addTeaNumber">添加新组员</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border :loading="loading" height="200" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
              </div>
            </div>
          </div>
          <div class="geneCon">
            <Tag type="dot" closable color="blue" v-for="(item,index) in geneArr" :key="index" :name="item" @on-close="handleClose">{{item}}</Tag>
            <Button size="small" type="primary" @click="addGene" icon="plus-round">添加基因</Button>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        browForm:{
          PanelID:'111',
          PanelRecID:'111',
          ProductName_cn:'111',
          PanelVisionID:'111',
          PanelOnline:'111',
          PanelStartDate:'2018-01-01',
          PanelFinishDate:'2018-01-01',
          PanelDesc:'111',
          productVariant:'111',
          productAnalisys:'111',
          productOpVi:'111',
          CreateDate:'2018-01-01',
          LatestUpdateDate:'2018-01-01'
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"组员姓名",
            key:'name',
            align: 'center'
          },
          {
            title:"E-mail",
            key:'email',
            align: 'center'
          },
          {
            title:"角色",
            key:'role',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            name:'111',
            email:'111@123.com',
            role:'111',
            CreateDate:'111',
            LatestUpdateDate:'111'
          }
        ],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10,
        geneArr:['基因1','基因2','基因3','基因4','基因1','基因2','基因3','基因4','基因1','基因2','基因3','基因4','基因1','基因2','基因3','基因4']
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editproductrec")
      },
      backList(){
        this.$router.push("/browproduct")
      },
      addTeaNumber(){
        //将当前受检者的ID传入
        this.$router.push("/addteamnumber2")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
      },
      handlePageSize(val){
        this.pageSize = val;
      },
      handleClose(event, name){
        // console.log(event);
        // console.log(name);
      },
      addGene(){
        this.$router.push("/addgene2")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .teamCon{
      box-sizing:border-box;
      
    }
    .geneCon{
      padding:10px;
      min-height:100px;
      border:1px dashed #ccc;
      .ivu-tag{
        height:auto;
        margin: -2px 10px 5px;
        padding: 3px 18px;
      }
      .ivu-btn{
        min-height:38px;
        padding:0;
      }
    }
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


