<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/panel">Panel管理</BreadcrumbItem>
      <BreadcrumbItem to="/browpanel">查看Panel</BreadcrumbItem>
      <BreadcrumbItem to="/browpanelrec">查看Panel升级记录</BreadcrumbItem>
      <BreadcrumbItem>添加新的基因</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的基因</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的基因</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="基因编号" prop="geneID">
            <Input v-model="addForm.geneID" placeholder="请输入基因编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因名称" prop="geneName">
            <Input v-model="addForm.geneName" placeholder="请输入基因名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          geneID:'',
          geneName:''
        },
        rule:{
          geneID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          geneName:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/browpanelrec")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


