<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem to="/browlibrary">查看文库</BreadcrumbItem>
      <BreadcrumbItem>添加新的变异</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的变异</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的变异</p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="文库公司编号" prop="libID">
            <Input v-model="addForm.libID" placeholder="请输入文库公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体名称" prop="ChrName">
            <Input v-model="addForm.ChrName" placeholder="请输入染色体名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体位置" prop="Position">
            <Input v-model="addForm.Position" placeholder="请输入染色体位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <Input v-model="addForm.Symbol" placeholder="请输入Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本">
            <Input v-model="addForm.transcript" placeholder="请输入转录本" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="REF序列">
            <Input v-model="addForm.ref" placeholder="请输入REF序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT序列">
            <Input v-model="addForm.alt" placeholder="请输入ALT序列" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异类型">
            <Input v-model="addForm.style" placeholder="请输入变异类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因型">
              <Input v-model="addForm.genotype" placeholder="请输入基因型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因频率">
              <Input v-model="addForm.geneFrequency" placeholder="请输入基因频率" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="测序深度">
              <Input v-model="addForm.genotype" placeholder="请输入测序深度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="突变结果">
              <Input v-model="addForm.geneFrequency" placeholder="请输入突变结果" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron">
              <Input v-model="addForm.exon" placeholder="请输入exon/intron" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="exon/intron位置">
              <Input v-model="addForm.exonPosition" placeholder="请输入exon/intron位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
              <Input v-model="addForm.hgvsp" placeholder="请输入HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="addForm.hgvsc" placeholder="请输入HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="rsID">
            <Input v-model="addForm.rsID" placeholder="rsID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="addForm.clini" placeholder="请输入临床意义" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="标记">
            <Input v-model="addForm.sign" placeholder="请输入标记" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          libID:'111',
          ChrName:'111',
          Position:'111',
          Symbol:'111',
          transcript:'111',
          ref:'111',
          alt:'111',
          style:'111',
          genotype:'111',
          geneFrequency:'111',
          exon:'111',
          exonPosition:'111',
          hgvsp:'111',
          hgvsc:'111',
          rsID:'111',
          clini:'111',
          sign:'111'
        },
        rule:{
          libID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          ChrName:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          Position:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          Symbol:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
              this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/browlibrary')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


