<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/product">产品管理</BreadcrumbItem>
      <BreadcrumbItem to="/browpruduct">查看产品</BreadcrumbItem>
      <BreadcrumbItem>添加新的产品升级记录</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的产品升级记录</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的产品升级记录</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="产品编号">
            <Input v-model="addForm.PanelID" readonly placeholder="请输入产品编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品中文名称">
            <Input v-model="addForm.ProductName_cn" readonly placeholder="请输入产品中文名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品版本号">
            <Input v-model="addForm.PanelVisionID" readonly placeholder="请输入产品版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品状态">
            <Input v-model="addForm.PanelOnline" readonly placeholder="请输入产品状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品上线日期">
            <DatePicker v-model="addForm.PanelStartDate" readonly placeholder="请选择产品上线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品下线日期">
            <DatePicker v-model="addForm.PanelFinishDate" readonly placeholder="请选择产品下线日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="产品检测的变异类型">
            <Input v-model="addForm.productVariant" readonly placeholder="请输入产品检测的变异类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品信息分析流程">
            <Input v-model="addForm.productAnalisys" readonly placeholder="请输入产品信息分析流程" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品信息分析流程版本号">
            <Input v-model="addForm.productOpVi" readonly placeholder="请输入产品信息分析流程版本号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="产品描述">
            <Input type="textarea" v-model="addForm.PanelDesc" readonly placeholder="请输入Panel描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          PanelID:'111',
          ProductName_cn:'111',
          PanelVisionID:'111',
          PanelOnline:'111',
          PanelStartDate:'2018-01-01',
          PanelFinishDate:'2018-01-01',
          PanelDesc:'111',
          productVariant:'111',
          productAnalisys:'111',
          productOpVi:'111',
        },
        rule:{
          PanelID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelVisionID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          PanelMethod:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/browproduct")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


