<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/nucleicacid">核酸管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :inline="true" :model="editForm" :rules="rule" label-position="right" :label-width="120">
         <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新时间">
            <Input v-model="editForm.LatestupdateDate" disabled  placeholder="请输入最后更新时间" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸公司编号" prop="nucleicAcidID">
            <Input v-model="editForm.nucleicAcidID" disabled  placeholder="请输入核酸公司编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取日期" prop="collectDate">
            <DatePicker v-model="editForm.collectDate" show-week-numbers  :options="DataPickerOption" placement="bottom-start" placeholder="请选择核酸提取日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="核酸提取人" prop="collector">
            <Input v-model="editForm.collector"  placeholder="请输入核酸提取人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量">
            <Input v-model="editForm.useRange"  placeholder="请输入核酸提取用量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量单位">
            <Input v-model="editForm.useRangeUnit"  placeholder="请输入核酸提取用量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积">
              <Input v-model="editForm.measure"  placeholder="请输入核酸体积" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积单位">
              <Input v-model="editForm.measureUnit"  placeholder="请输入核酸体积单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度">
            <Input v-model="editForm.density"  placeholder="请输入核酸浓度" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度单位">
            <Input v-model="editForm.densityUnit"  placeholder="请输入核酸浓度单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量">
            <Input v-model="editForm.total"  placeholder="请输入核酸总量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量单位">
            <Input v-model="editForm.totalUnit"  placeholder="请输入核酸总量单位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸质量">
            <Select v-model="editForm.quality"  placeholder="请选择核酸质量" style="width: 280px">
              <Option value="好">好</Option>
              <Option value="中">中</Option>
              <Option value="坏">坏</Option>
            </Select>
          </FormItem>
          <FormItem label="核酸类型">
            <Input v-model="editForm.nucleicAcidStyle"  placeholder="请输入核酸类型" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Select v-model="editForm.status"  placeholder="请选择状态" style="width: 280px">
              <Option value="合格">合格</Option>
              <Option value="不合格">不合格</Option>
            </Select>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('editForm')">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestupdateDate:'111',
          nucleicAcidID:'111',
          useRange:'111',
          useRangeUnit:'111',
          collector:'111',
          collectDate:'2018-01-01',
          measure:'111',
          measureUnit:'111',
          density:'111',
          densityUnit:'111',
          total:'111',
          totalUnit:'111',
          quality:'好',
          nucleicAcidStyle:'111',
          status:'合格'
        },
        rule:{
          nucleicAcidID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"核酸公司编号",
            key:'nucleiID',
            align: 'center'
          },
          {
            title:"核酸提取用量及单位",
            key:'useTotal',
            align: 'center'
          },
          {
            title:"核酸提取日期",
            key:'collectDate',
            align: 'center'
          },
          {
            title:"核酸提取人",
            key:'collector',
            align: 'center'
          },
          {
            title:"核酸体积及单位",
            key:'nucleiMeasure',
            align: 'center'
          },
          {
            title:"核酸浓度及单位",
            key:'nucleiDensity',
            align: 'center'
          },
          {
            title:"核酸总量及单位",
            key:'nucleiTotal',
            align: 'center'
          },
          {
            title:"核酸质量",
            key:'nucleicQuality',
            align: 'center'
          },
          {
            title:"状态",
            key:'status',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            nucleiID:'111',
            useTotal:'111',
            collectDate:'111',
            collector:'111',
            nucleiMeasure:'111',
            nucleiDensity:'111',
            nucleiTotal:'111',
            nucleicQuality:'111',
            status:'111',
            CreateDate:'111',
            LatestUpdateDate:'111'
          }
        ],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push('/nucleicacid')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


