<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/nucleicacid">核酸管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :inline="true" :model="browForm" label-position="right" :label-width="120">
         <FormItem label="创建日期">
            <Input v-model="browForm.CreateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新时间">
            <Input v-model="browForm.LatestupdateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸公司编号" prop="nucleicAcidID">
            <Input v-model="browForm.nucleicAcidID" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取日期" prop="collectDate">
            <Input v-model="browForm.collectDate" readonly style="width: 280px"></Input>
          </FormItem>
          </FormItem>
          <FormItem label="核酸提取人" prop="collector">
            <Input v-model="browForm.collector" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量">
            <Input v-model="browForm.useRange" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸提取用量单位">
            <Input v-model="browForm.useRangeUnit" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积">
              <Input v-model="browForm.measure" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸体积单位">
              <Input v-model="browForm.measureUnit" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度">
            <Input v-model="browForm.density" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸浓度单位">
            <Input v-model="browForm.densityUnit" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量">
            <Input v-model="browForm.total" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸总量单位">
            <Input v-model="browForm.totalUnit" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸质量">
            <Input v-model="browForm.quality" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸类型">
            <Input v-model="browForm.nucleicAcidStyle" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Input v-model="browForm.status" readonly style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="addContainer">
            <Button type="warning" size="large" @click="addLibrary">添加新文库</Button>
          </div>
          <div class="chromoListcontainer">
            <Table stripe border height="200" :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
            <div class="batchContainer">
              <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
            </div>
            <div class="pageContainer">
              <Page show-total show-sizer :total="TotalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        browForm:{
          CreateDate:'111',
          LatestupdateDate:'111',
          nucleicAcidID:'111',
          useRange:'111',
          useRangeUnit:'111',
          collector:'111',
          collectDate:'2018-01-01',
          measure:'111',
          measureUnit:'111',
          density:'111',
          densityUnit:'111',
          total:'111',
          totalUnit:'111',
          quality:'好',
          nucleicAcidStyle:'111',
          status:'合格'
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"文库公司编号",
            key:'libID',
            align: 'center'
          },
          {
            title:"文库构建用量及单位",
            key:'useRange',
            align: 'center'
          },
          {
            title:"文库构建日期",
            key:'CollectDate',
            align: 'center'
          },
          {
            title:"文库构建人",
            key:'Collector',
            align: 'center'
          },
          {
            title:"文库质量",
            key:'libQuality',
            align: 'center'
          },
          {
            title:"文库体积及单位",
            key:'libMeasure',
            align: 'center'
          },
          {
            title:"文库浓度及单位",
            key:'libDensity',
            align: 'center'
          },
          {
            title:"文库总量及单位",
            key:'libTotal',
            align: 'center'
          },
          {
            title:"文库插入片段大小",
            key:'libInsert',
            align: 'center'
          },
          {
            title:"状态",
            key:'status',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            libID:'111',
            useRange:'111',
            CollectDate:'111',
            Collector:'111',
            libQuality:'111',
            libMeasure:'111',
            libDensity:'111',
            libTotal:'111',
            libInsert:'111',
            status:'111',
            CreateDate:'111',
            LatestUpdateDate:'111'
          }
        ],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editnucleicacid")
      },
      backList(){
        this.$router.push('/nucleicacid')
      },
      addLibrary(){
        this.$router.push("/addbiospeciemens")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/browbiospeciemens")
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/editbiospeciemens")
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
      },
      handlePageSize(val){
        this.pageSize = val;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .chromoListcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:5px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding: 2px;
      }
    }
  }
}
</style>


