<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/subject">受检者管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的临床诊疗</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的临床诊疗</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的临床诊疗</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="受检者编号" prop="SubjectName">
            <Input v-model="addForm.SubjectID" placeholder="请输入受检者编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床诊疗描述">
            <Input v-model="addForm.diagDesc" type="textarea" placeholder="请输入临床诊疗描述" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病分级">
            <Input v-model="addForm.dislevel" placeholder="请输入疾病分级" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病分期">
            <Input v-model="addForm.disPeriod" placeholder="请输入疾病分期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病治疗历史">
            <Input v-model="addForm.disHistory" placeholder="请输入疾病治疗历史" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          SubjectID:'',
          diagDesc:'',
          dislevel:'',
          disPeriod:'',
          disHistory:''
        },
        rule:{
          SubjectName:[
            {
              required: true, message: '染色体名称不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
              this.$router.push('/basicdataModal/chromo')
            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/browsubject")
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


