<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/biospeciemens">样本管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的样本</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的样本</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的样本</p>
        <Form ref="addForm" :inline="true" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="分子诊断编号" prop="molDiagnosisID">
            <Input v-model="addForm.molDiagnosisID" placeholder="请输入分子诊断编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="病理编号" prop="pathologyID">
            <Input v-model="addForm.pathologyID" placeholder="请输入病理编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本类型">
            <Select v-model="addForm.bioStyle" placeholder="请选择样本类型" style="width: 280px">
              <Option value="1111">1111</Option>
              <Option value="2222">2222</Option>
            </Select>
          </FormItem>
          <FormItem label="取样部位">
            <Input v-model="addForm.samplingSite" placeholder="请输入取样部位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="取样日期">
            <DatePicker type="daterange" v-model="addForm.samplingDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择取样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样日期">
            <DatePicker type="daterange" v-model="addForm.collectionDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择收样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样人">
              <Input v-model="addForm.Sampler" placeholder="请输入收样人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="是否为肿瘤细胞">
              <Input v-model="addForm.isCancerCell" placeholder="请选择是否为肿瘤细胞" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="肿瘤细胞含量">
            <Input v-model="addForm.cancerCellPrecent" placeholder="请输入肿瘤细胞含量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Input v-model="addForm.status" placeholder="请输入肿瘤细胞含量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本量">
            <Input v-model="addForm.sampleMount" placeholder="请输入样本量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本量单位">
            <Input v-model="addForm.sampleMountUnit" placeholder="请输入样本量单位" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        addForm: {
          molDiagnosisID:'',
          pathologyID:'',
          bioStyle:'',
          samplingSite:'',
          samplingDate:'',
          collectionDate:'',
          Sampler:'',
          isCancerCell:'',
          cancerCellPrecent:'',
          status:'',
          sampleMount:'',
          sampleMountUnit:'',
          
        },
        rule:{
          molDiagnosisID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ],
          pathologyID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {

            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/biospeciemens")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    h2{
      margin:0 0 10px 30px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


