<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/moldiagnosis">样本管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请输入创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新时间">
            <Input v-model="editForm.LatestupdateDate" disabled placeholder="请输入最后更新时间" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="分子诊断编号">
            <Input v-model="editForm.molDiagnosisID" disabled placeholder="请输入分子诊断编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本编号">
            <Input v-model="editForm.bioID" disabled placeholder="请输入样本编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="病理编号">
            <Input v-model="editForm.pathologyID" placeholder="请输入病理编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本类型">
            <Select v-model="editForm.bioStyle" placeholder="请选择样本类型" style="width: 280px">
              <Option value="111">111</Option>
              <Option value="222">222</Option>
            </Select>
          </FormItem>
          <FormItem label="取样部位">
            <Input v-model="editForm.samplingSite" placeholder="请输入取样部位" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="取样日期">
            <DatePicker type="daterange" v-model="editForm.samplingDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择取样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样日期">
            <DatePicker type="daterange" v-model="editForm.collectionDate" show-week-numbers :options="DataPickerOption" placement="bottom-start" placeholder="请选择收样日期" style="width: 280px"></DatePicker>
          </FormItem>
          <FormItem label="收样人">
              <Input v-model="editForm.Sampler" placeholder="请输入收样人" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="是否为肿瘤细胞">
              <Input v-model="editForm.isCancerCell" placeholder="请选择是否为肿瘤细胞" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="肿瘤细胞含量">
            <Input v-model="editForm.cancerCellPrecent" placeholder="请输入肿瘤细胞含量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本量">
            <Input v-model="editForm.sampleMount" placeholder="请输入样本量" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="样本量单位">
            <Input v-model="editForm.sampleMountUnit" placeholder="请输入样本量单位" style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleSubmit">保存</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
    },
    data() {
      return {
        DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        editForm:{
          CreateDate:'111',
          LatestupdateDate:'111',
          molDiagnosisID:'111',
          bioID:'111',
          pathologyID:'111',
          bioStyle:'111',
          samplingSite:'111',
          samplingDate:'111',
          collectionDate:'111',
          Sampler:'111',
          isCancerCell:'111',
          cancerCellPrecent:'111',
          status:'111',
          sampleMount:'111',
          sampleMountUnit:'111',
        },
        rule:{
          SubjectID:[
            {
              required: true, message: '不能为空', trigger: 'blur'
            }
          ]
        },
        loading: true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"核酸公司编号",
            key:'nucleiID',
            align: 'center'
          },
          {
            title:"核酸提取用量及单位",
            key:'useTotal',
            align: 'center'
          },
          {
            title:"核酸提取日期",
            key:'collectDate',
            align: 'center'
          },
          {
            title:"核酸提取人",
            key:'collector',
            align: 'center'
          },
          {
            title:"核酸体积及单位",
            key:'nucleiMeasure',
            align: 'center'
          },
          {
            title:"核酸浓度及单位",
            key:'nucleiDensity',
            align: 'center'
          },
          {
            title:"核酸总量及单位",
            key:'nucleiTotal',
            align: 'center'
          },
          {
            title:"核酸质量",
            key:'nucleicQuality',
            align: 'center'
          },
          {
            title:"状态",
            key:'status',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            nucleiID:'111',
            useTotal:'111',
            collectDate:'111',
            collector:'111',
            nucleiMeasure:'111',
            nucleiDensity:'111',
            nucleiTotal:'111',
            nucleicQuality:'111',
            status:'111',
            CreateDate:'111',
            LatestUpdateDate:'111'
          }
        ],
        TotalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
       handleSubmit(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
              this.$router.push('/moldiagnosis')
            } else {
                this.$Message.error('Fail!');
            }
        })
      },
      backList(){
        this.$router.push("/biospeciemens")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


