<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/library">文库管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :inline="true" :model="browForm" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="browForm.CreateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
            <Input v-model="browForm.LatestUpdateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="核酸公司编号" prop="nucleicAcidID">
            <Input v-model="browForm.nucleicAcidID" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库公司编号" prop="libID">
            <Input v-model="browForm.libID" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建人">
              <Input v-model="browForm.collector" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建日期">
            <Input v-model="browForm.collectDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建用量">
            <Input v-model="browForm.useRange" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库构建用量单位">
            <Input v-model="browForm.useRangeUnit" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积">
              <Input v-model="browForm.measure" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库体积单位">
              <Input v-model="browForm.measureUnit" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度">
              <Input v-model="browForm.density" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库浓度单位">
              <Input v-model="browForm.densityUnit" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量">
            <Input v-model="browForm.total" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库总量单位">
            <Input v-model="browForm.totalUnit" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小">
            <Input v-model="browForm.insert" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库插入片段大小单位">
            <Input v-model="browForm.insertUnit" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文库质量">
            <Input v-model="browForm.quality" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="状态">
            <Input v-model="browForm.status" readonly style="width: 280px"></Input>
          </FormItem>
          <div>
            <FormItem :label-width="10">
              <Button type="primary" @click="handleEdit">编辑</Button>
              <Button @click="backList">返回</Button>
            </FormItem>
          </div>
        </Form>
        <div class="relateContainer">
          <div class="qcCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addQC">添加新QC结果</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border height="200" :loading="loading" ref="selection" :columns="qcTableHead" :data="qcTableData" @on-selection-change="qcHandleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn">移除选中的{{qcMultipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="qcTotalNum" :current="qcPageNum" :page-size="qcPageSize" show-elevator placement="top" size="small" @on-change="qcHandlePage" @on-page-size-change='qcHandlePageSize'></Page>
              </div>
            </div>
          </div>
          <div class="variantCon">
            <div class="addContainer">
              <Button type="warning" size="large" @click="addVariant">添加新变异</Button>
            </div>
            <div class="listcontainer">
              <Table stripe border height="200" :loading="molLoading" ref="selection" :columns="variantTableHead" :data="variantTableData" @on-selection-change="variantHandleSelectionChange"></Table>
              <div class="batchContainer">
                <Button type="text" size="large" class="batchBtn">移除选中的{{variantMultipleSelection.length}}个条目</Button>
              </div>
              <div class="pageContainer">
                <Page show-total show-sizer :total="variantTotalNum" :current="variantPageNum" :page-size="variantPageSize" show-elevator placement="top" size="small" @on-change="variantHandlePage" @on-page-size-change='variantHandlePageSize'></Page>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.loading = false;
      this.molLoading = false;
    },
    data() {
      return {
         DataPickerOption: {
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        browForm:{
          CreateDate:'111',
          LatestUpdateDate:'111',
          nucleicAcidID:'111',
          libID:'111',
          collector:'111',
          collectDate:'111',
          useRange:'111',
          useRangeUnit:'111',
          measure:'111',
          measureUnit:'111',
          density:'111',
          densityUnit:'111',
          total:'111',
          totalUnit:'111',
          insert:'111',
          insertUnit:'111',
          quality:'111',
          status:'111'
        },
        loading: true,
        molLoading: true,
        qcTableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"总碱基数",
            key:'totalbase',
            align: 'center'
          },
          {
            title:"总序列数量",
            key:'totalSequence',
            align: 'center'
          },
          {
            title:"重复率",
            key:'repetitionRate',
            align: 'center'
          },
          {
            title:"对比率",
            key:'compareRate',
            align: 'center'
          },
          {
            title:"捕获效率",
            key:'captureEfficiency',
            align: 'center'
          },
          {
            title:"探针平均深度",
            key:'probeAverage',
            align: 'center'
          },
          {
            title:"探针均一性",
            key:'probeHomogeneity',
            align: 'center'
          },
          {
            title:"覆盖率",
            key:'coverRate',
            align: 'center'
          },
          {
            title:"覆盖率≥30",
            key:'coverRate30',
            align: 'center'
          },
          {
            title:"状态",
            key:'status',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.qcShow(params.row.diagnosisID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.qcEdit(params.row.diagnosisID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.qcRemove(params.row.diagnosisID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        qcTableData:[
          {
            totalbase:'111',
            totalSequence:'111',
            repetitionRate:'111',
            compareRate:'111',
            captureEfficiency:'111',
            probeAverage:'111',
            probeHomogeneity:'111',
            coverRate:'111',
            coverRate30:'111',
            status:'111'
          }
        ],
        qcTotalNum:0,
        qcMultipleSelection:[],
        qcPageNum:1,
        qcPageSize:10,
        variantTableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"Gene Symbol",
            key:'Symbol',
            align: 'center'
          },
          {
            title:"染色体名称及位置",
            key:'namePosition',
            align: 'center'
          },
          {
            title:"变异类型",
            key:'style',
            align: 'center'
          },
          {
            title:"基因型",
            key:'genotype',
            align: 'center'
          },
          {
            title:"基因频率",
            key:'geneFrequency',
            align: 'center'
          },
          {
            title:"exon/intron 及位置",
            key:'exon',
            align: 'center'
          },
          {
            title:"HGVSp",
            key:'hgvsp',
            align: 'center'
          },
          {
            title:"HGVSc",
            key:'hgvsc',
            align: 'center'
          },
          {
            title:"临床意义",
            key:'clini',
            align: 'center'
          },
          {
            title:"标记",
            key:'sign',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            width:100,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.variantShow(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.variantEdit(params.row.ChrID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    fontSize:'16px',
                    padding: '3px'
                  },
                  on: {
                    click: () => {
                      this.variantRemove(params.row.ChrID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        variantTableData:[
          {
            Symbol:'111',
            namePosition:'111',
            style:'111',
            genotype:'111',
            geneFrequency:'111',
            exon:'111',
            hgvsp:'111',
            hgvsc:'111',
            clini:'111',
            sign:'111'
          }
        ],
        variantTotalNum:0,
        variantMultipleSelection:[],
        variantPageNum:1,
        variantPageSize:10
      }
    },
    methods: {
      handleEdit(){
        this.$router.push("/editlibrary")
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            
          }
        })
      },
      backList(){
        this.$router.push("/library")
      },
      addQC(){
        //将当前受检者的ID传入
        this.$router.push("/addqc")
      },
      qcShow (id) {
        // console.log(id);
        this.$router.push("/browqc")
      },
      qcEdit (id) {
        // console.log(id);
        this.$router.push("/editqc")
      },
      qcRemove(id){
        // console.log(id);        
      },
      qcHandleSelectionChange(val){
        this.qcMultipleSelection = val;
      },
      qcHandlePage(val){
        this.qcPageNum = val;
      },
      qcHandlePageSize(val){
        this.qcPageSize = val;
      },
      addVariant(){
        //将当前受检者的ID传入
        this.$router.push("/addvariant")
      },
      variantShow (id) {
        // console.log(id);
        this.$router.push("/browvariant")
      },
      variantEdit (id) {
        // console.log(id);
        this.$router.push("/editvariant")
      },
      variantRemove(id){
        // console.log(id);        
      },
      variantHandleSelectionChange(val){
        this.variantMultipleSelection = val;
      },
      variantHandlePage(val){
        this.variantPageNum = val;
      },
      variantHandlePageSize(val){
        this.variantPageSize = val;
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-form{
      border-bottom:1px dashed #ccc;
    }
    .ivu-btn{
      min-width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relateContainer{
    .qcCon{
      box-sizing:border-box;
      border-bottom:1px dashed #ccc;
    }
    .addContainer{
      padding:10px;
      text-align:right;
    }
    .listcontainer{
      .ivu-table-body{
        overflow:hidden;
        .ivu-table-cell{
          padding:0px !important;
        }
      }
      .batchContainer{
        padding:2px;
        .batchBtn{
          color:@themeColor;
          &:hover{
            color:@warningColor;
          }
        }
      }
      .pageContainer{
        text-align:right;
        padding-bottom: 10px;
      }
    }
  }
}
</style>


